<template>
  <div id="app">
    <!--  Loader  loading  -->
    <transition name="fade">
      <Loader v-if="isLoading" :visible="isLoading" />
    </transition>

    <!--  -->
    <!-- <transition name="fade"> -->
      <router-view class="view" v-if="isView"></router-view>
    <!-- </transition> -->
  </div>
</template>

<script>
import Loader from "@/components/Loader.vue";
import { mapState, mapActions } from 'vuex';

export default {
  data() {
    return {
      isLoading: false,  // 
      isView: false,   //  router-view 
    };
  },
  components: {
    Loader,
  },
  watch: {
    // 
    $route(to) {
      this.onRouteChange(to);
    }
  },
  computed: {
    ...mapState(['hasVisitedHome']), //  Vuex  hasVisitedHome 
  },
  methods: {
    ...mapActions(['markVisitedHome']), //  Vuex  action
    onRouteChange(to) {

      
      //  home 
      if (to.name === 'Home' && !this.hasVisitedHome) {
  
        this.isLoading = true;  // 
        this.isView = false;  //  router-view
     
        // 
        setTimeout(() => {
         
          this.isLoading = false;  // 
          
          this.markVisitedHome();  //  Vuex 
        }, 3000); // 
        setTimeout(() => {
          this.isView = true;  //  router-view
        },4000)
      } else {
        this.isLoading = false;  //  home
        this.isView = true;  //  router-view
      }
    }
  },
  mounted() {
    // 
    this.onRouteChange(this.$route);
  }
};
</script>

<style scoped>
/*  loader  fade  */
.fade-enter-active, .fade-leave-active {
  transition: opacity 1s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active in <2.1.8 */ {
  opacity: 0;
}
</style>



<style>
.fade-enter-active,
.fade-leave-active {
  transition: opacity 1s;
}

#app {
  background-color: var(--background-color);
  transition: background-color 0.5s ease, color 0.5s ease;
  height: 100vh;
  min-width: 320px;
}

.fade-enter,
.fade-leave-to {
  opacity: 0;
}

a {
  text-decoration: none !important;
  line-height: 0 !important;
}
</style>
